.faq{
  &__items{
    margin-top: 3em;
  }
}

.qa-card{
  padding: 1.5em;
  border-radius: 1em;
  background-color: #fff;
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    text-transform: uppercase;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
    }
  }
  &__hidden{
    margin-top: 1em;
    display: none;
  }
  &._active{
    .qa-card__btn{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.contacts{
  &__links{
    display: flex;
    justify-content: center;
    margin-top: 2em;
  }
  &__link{
    @include lastM(0 3em 0 0);
  }
}

.m-link{
  display: flex;
  align-items: center;
  color: $primary;
  &:hover{
    span{
      text-decoration-color: transparent;
    }
  }
  img{
    width: 1.5em;
    margin-right: .5em;
  }
  span{
    font-size: 1em;
    line-height: 150%;
    text-decoration: underline;
    transition: inherit;
  }
}


