.pagination{
  margin-top: 3em;
  display: flex;
  justify-content: center;
  &__item{
    height: 2.5em;
    min-width: 2.5em;
    padding: 0 .25em;
    border-radius: 0.5em;
    background-color: $anti-flash-white;
    @include center;
    margin: 0 .5em;
    font-weight: 500;
    font-size: 1em;
    line-height: 150%;
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $onyx;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .pagination{
    ._hide-mob{
      display: none;
    }
    ._btn{
      width: 2.8125em;
      padding: 0;
    }
    &__item{
      height: 2.8125em;
      min-width: 2.8125em;
      padding: 0 .875em;
      span{
        font-size: .875em;
      }
    }
  }
}
