.partners-program{
  &__empty{
    margin-top: 3em;
    text-align: center;
  }
}

.partner-info{
  margin-top: 3em;
  padding: 3em;
  background-color: #fff;
  box-shadow: $shadow;
  border-radius: 1.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__link{
    display: flex;
    align-items: center;
  }
  .copy-btn{
    margin-left: 1em;
  }
  &__right{
    display: flex;
    align-items: center;
  }
  &__items{
    display: flex;
  }
  &__item{
    @include lastM(0 3em 0 0);
    text-align: center;
  }
  &__btn{
    margin-left: 3em;
    flex-shrink: 0;
  }
}

.copy-btn{
  width: 1.5em;
  flex-shrink: 0;
  @include center;
  img{
    display: block;
    width: 100%;
  }
}

.filter{
  margin: 3em 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:first-child{
    margin-top: 0;
  }
  .tabs{
    margin-left: 1.5em;
  }
  .tab{
    margin-top: 0;
  }
  &__btn{
    margin-left: .5em;
  }
  .m-select{
    margin-left: .5em;
    min-width: 12.5em;
  }
}

.m-select{
  input,
  select{
    cursor: pointer;
    font-family: $font1;
    width: 100%;
    padding: 0 3.5em 0 1.5em;
    border-radius: .5em;
    border: 2px solid $anti-flash-white;
    height: 2.5em;
    font-size: 1em;
    line-height: 150%;
    background-color: transparent;
    background-image: url(../img/arrow2.svg);
    background-repeat: no-repeat;
    background-position: right 1.5em center;
    background-size: 1.5em auto;
    -webkit-appearance: none;
    -moz-appearance:    none;
    appearance:         none;
    &:focus{
      border-color: $onyx;
    }
  }
}

.m-table{
  &__wrap{
    background-color: #fff;
    box-shadow: $shadow;
    padding: 3em;
    border-radius: 1.5em;
  }
  table{
    width: 100%;
    border-collapse: collapse;
  }
  tbody{
    tr:last-child{
      td{
        padding-bottom: 0;
      }
    }
  }
  th,td{
    font-size: 1em;
    line-height: 150%;
    font-weight: 400;
  }
  th{
    color: $cadet-gray;
    padding-bottom: 1em;
    text-align: left;
  }
  td{
    padding-top: 1em;
    padding-bottom: 1em;
    border-top: 1px solid $timberwolf;
  }
}

.program-table{
  th,td{
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(5){
      width: 15.5em;
      padding-right: 1em;
    }
    &:nth-child(5){
      padding-right: 0;
      padding-left: 1em;
      text-align: right;
    }
  }
}

.m-status{
  font-weight: 500;
  &._green{
    color: $mantis;
  }
  &._red{
    color: $imperial-red;
  }
}

.program-table2{
  th,td{
    width: 25%;
    padding-right: 1em;
    &:last-child{
      padding-right: 0;
      text-align: right;
    }
  }
}

.exchange-table{
  .m-status{
    text-decoration: underline;
    &:hover{
      color: $primary;
    }
  }
  th,td{
    &:nth-child(1),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5){
      width: 15.5em;
      padding-right: 1em;
    }
    &:nth-child(5){
      padding-right: 0;
      padding-left: 1em;
      text-align: right;
    }
  }
}


@media screen and (max-width: 1400px){
  .partner-info{
    padding: 2.25em;
  }

  .program-table{
    th,td{
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(4),
      &:nth-child(5){
        width: 12.875em;
      }
    }
  }

  .exchange-table{
    th,td{
      &:nth-child(1),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5){
        width: 12.875em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .partner-info{
    display: block;
    border-radius: .75em;
    padding: 1.5em;
    &__left{
      text-align: center;
      margin-bottom: 1.5em;
    }
    &__btn{
      margin-left: 3em;
    }
    &__link{
      justify-content: center;
    }
    &__items{
      justify-content: space-between;
      flex-grow: 1;
    }
    &__item{
      @include lastM(0 2em 0 0);
    }
  }

  .filter{
    display: block;
    &__left{
      justify-content: space-between;
    }
    &__right{
      margin-top: 1.5em;
    }
    &__selects{
      flex-grow: 1;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
    .m-select{
      margin: 0;
    }
    .tab{
      &:last-child{
        margin-right: 0;
      }
    }
  }

  .m-table{
    overflow-x: auto;
    padding: 1em 2em;
    margin-top: -1em;
    margin-bottom: -1em;
    margin-left: -2em;
    width: calc(100% + 4em);
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    &__wrap{
      border-radius: .75em;
      padding: 1.5em;
    }
  }

  .program-table{
    .m-table__wrap{
      min-width: 69.5em;
    }
    th,td{
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(4),
      &:nth-child(5){
        width: 11em;
      }
    }
  }

  .program-table2 {
    .m-table__wrap {
      min-width: 55em;
    }
  }

  .exchange-table{
    .m-table__wrap{
      min-width: 75em;
    }
    th,td{
      &:nth-child(1),
      &:nth-child(3),
      &:nth-child(5){
        width: 11em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .partner-info{
    @include fullScreenMob;
    overflow: hidden;
    border-radius: 0;
    &__right{
      display: block;
    }
    &__btn{
      margin: 1.5em auto 0;
    }
    &__items{
      flex-wrap: wrap;
      width: calc(100% + 1.5em);
      margin-top: -1.5em;
      justify-content: center;
      flex-grow: unset;
    }
    &__item{
      width: calc(50% - 1.5em);
      margin: 1.5em 1.5em 0 0 !important;
    }
  }

  .filter{
    display: block;
    &__left{
      flex-direction: column;
    }
    .tabs{
      margin-top: 1.5em;
    }
    &__right{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: .5em 0 0;
    }
    &__selects{
      grid-template-columns: 100%;
    }
  }

  .m-table{
    padding: 1em 0;
    margin-left: -1em;
    width: calc(100% + 2em);
    &__wrap{
      border-radius: 0;
      padding: 1.5em 1em;
    }
  }

  .program-table {
    .m-table__wrap {
      min-width: 68.25em;
    }
  }
}
