.history{
  &__grid{
    margin-top: 2em;
  }
}

.history-card{
  padding: 1.5em;
  background-color: #fff;
  border-radius: 1em;
  text-align: center;
  &__wrap{
    display: grid;
    grid-template-columns: calc(50% - 1.75em) 1.5em calc(50% - 1.75em);
    gap: 1em;
    margin-top: 1em;
    align-items: center;
  }
  &__arrows{
    width: 100%;
    display: block;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__icon{
    height: 1.5em;
    margin-bottom: .5em;
  }
  &__desc{
    @include lineTruncate;
  }
}

.about{
  &__col1{
    max-width: 41.5em;
  }
  &__col2{
    align-items: flex-start;
    position: relative;
  }
  .text{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__map{
    img{
      width: 100%;
      display: block;
      filter: drop-shadow(0 .5em 1.5em rgba(49, 54, 56, 0.2));
    }
  }
  &__bottom{
    justify-content: space-between;
    position: relative;
    z-index: 2;
    margin-top: -.75em;
  }
  &__logo{
    width: 10.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.about-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 500;
  text-transform: uppercase;
  @include lastM(0 1.5em 0 0);
  img{
    height: 1.5em;
    margin-bottom: .5em;
  }
}


@media screen and (max-width: 1400px){
  .about{
    &__col1{
      max-width: 34em;
    }
    &__bottom{
      margin-top: 2.8125em;
      flex-direction: column;
    }
    &__items{
      margin-top: 3em;
      width: 100%;
      justify-content: center;
    }
  }

  .about-item{
    @include lastM(0 3em 0 0);
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
      gap: 3em;
    }
    &__col1{
      max-width: 100%;
    }
    &__nav{
      justify-content: center;
    }
    &__bottom{
      margin-top: 1.5em;
    }
    &__items{
      justify-content: space-between;
      margin-top: 1.5em;
    }
  }

  .history{

  }
}


@media screen and (max-width: 576px){
  .about{
    &__items{
      flex-direction: column;
      align-items: center;
    }
  }

  .about-item{
    @include lastM(0 0 1.5em);
  }
}
