@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.socials{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 1em 0 0);
    text-transform: unset;
    img{
      margin-right: 1em;
      width: 1.5em;
    }
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    border-radius: inherit;
  }
}


@media screen and (max-width: 576px){
  .socials{
    &__item{
      padding: 0;
      width: 3.5em;
      img{
        margin: 0;
      }
      span{
        display: none;
      }
    }
  }
}
