.page-block{
  padding: 4.5em 0 3em;
}

.reviews{
  &__items{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: calc(100% + 1em);
    margin-top: 1em;
  }
}

.review-card{
  @include center;
  width: calc(100% / 6 - 1em);
  margin: 1em 1em 0 0;
  padding: .5em 1em;
  border-radius: 1em;
  background-color: #fff;
  height: 5em;
  @media screen and (min-width: 577px){
    &.review-card--medium{
      width: 17.0625em;
    }
    &.review-card--big{
      width: 23.75em;
    }
  }
  img{
    max-width: 100%;
    max-height: 3em;
  }
}



@media screen and (max-width: 1400px){
  .review-card{
    width: calc(25% - 1em);
  }
}


@media screen and (max-width: 992px){
  .review-card{
    width: calc(50% - 1em);
  }
}


@media screen and (max-width: 576px){
  .review-card{
    width: calc(100% - 1em);
  }
}
