.about-page{
  &__items{
    margin-top: 2em;
    align-items: center;
  }
  &__desc{
    margin-top: 2em;
  }
}

.service-card,
.about-card{
  padding: 1.5em 1.5em 1.5em 2.75em;
  background-color: #fff;
  border-radius: 1em;
  width: calc(100% - 1.75em);
  margin-left: auto;
  position: relative;
  &__icon{
    position: absolute;
    margin: 0;
    top: 50%;
    left: 0;
    transform: translate(-50%,-50%);
  }
}

.about-card{
  &__desc{
    margin-top: 1em;
  }
}

.about-services{
  margin-top: 4.5em;
  &__items{
    margin-top: 2em;
  }
}

.geography{
  margin-top: 3em;
  &__desc{
    margin-top: 2em;
    text-align: center;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1em;
    width: calc(100% + 1em);
    justify-content: center;
  }
}

.geography-item{
  margin: 1em 1em 0 0;
  border-radius: 1em;
  background-color: #fff;
  padding: 1em 1.5em;
  display: flex;
  align-items: center;
  &__flag{
    border: $border;
    border-radius: 50%;
    width: 1.5em;
    height: 1.5em;
    margin-right: 1em;
    flex-shrink: 0;
  }
}
