.m-dropdown{
  position: relative;
  &._open{
    z-index: 5;
    .m-dropdown__show img,
    .m-dropdown__show svg{
      transform: rotate(-180deg);
    }
    .m-dropdown__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    cursor: pointer;
    text-transform: unset;
    img{
      width: 1.5em;
      margin-left: .5em;
      transition: all .3s;
    }
  }
  &__hidden{
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    padding: 1.5em;
    left: 50%;
    transform: translateX(-50%);
    top: 100%;
    margin-top: .5em;
    background-color: #fff;
    box-shadow: $shadow2;
    border-radius: 1em;
    border: $border;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__item{
    @include lastM(0 0 1em);
  }
}

.header{
  padding: 1.5em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 10.5em;
    margin-right: 3em;
    img{
      width: 100%;
      display: block;
    }
  }
  .lang{
    margin-left: .5em;
  }
  &__login{
    text-transform: unset;
  }
}

.burger{
  display: none;
}

.is-online{
  display: flex;
  align-items: center;
  font-weight: 500;
  svg{
    width: 1em;
    margin-right: .5em;
  }
  &__title{
    color: $mantis;
  }
  &._offline{
    .is-online__title{
      color: $imperial-red;
    }
    ._fill{
      fill: $imperial-red;
    }
    ._stroke{
      stroke: $imperial-red;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  margin-right: 3em;
  &__item,
  &__link{
    @include lastM(0 3em 0 0);
  }
  &__show,
  &__link {
    font-size: 1em;
    line-height: 150%;
    transition: all .3s;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  &__link{
    &--active{
      color: $primary;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    svg{
      width: 1.5em;
      margin-left: .5em;
    }
  }
  .m-dropdown__item{
    white-space: nowrap;
  }
}

.menu-tablet{
  &__show{
    display: none;
  }
}

.header-user{
  .m-dropdown__item{
    white-space: nowrap;
  }
}


@media screen and (max-width: 1400px){
  .header{
    &__logo{
      margin-right: 2.25em;
    }
  }

  .nav{
    margin-right: 2.25em;
    &__item,
    &__link{
      @include lastM(0 2.25em 0 0);
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      margin-right: 1.5em;
    }
    .lang{
      margin-left: 1em;
    }
  }

  .menu-tablet{
    @media screen and (min-width: 577px){
      margin-right: 1.5em;
      position: relative;
      &._open{
        .nav{
          opacity: 1;
          pointer-events: unset;
        }
        .menu-tablet__show img{
          transform: rotate(-180deg);
        }
      }
      &__show{
        cursor: pointer;
        display: flex;
        align-items: center;
        img{
          width: 1.5em;
          margin-left: .5em;
          transition: all .3s;
        }
      }
    }
  }

  .nav{
    flex-direction: column;
    @media screen and (min-width: 577px){
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      margin-top: .5em;
      z-index: 5;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      padding: 1.5em;
      background-color: #fff;
      box-shadow: $shadow2;
      border-radius: 1em;
      border: $border;
    }
    &__link{
      display: none;
    }
    &__item{
      @include lastM(0 0 2em);
      white-space: nowrap;
    }
    .m-dropdown{
      &__show{
        text-transform: uppercase;
        font-weight: 500;
        justify-content: center;
        svg{
          display: none;
        }
      }
      &__hidden{
        opacity: 1;
        pointer-events: unset;
        background-color: transparent;
        position: static;
        box-shadow: unset;
        border: none;
        transform: unset;
        padding: 0;
        margin-top: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
    &__inner{
      position: relative;
    }
    .lang{
      display: none;
    }
    &__login{
      margin-top: 1em;
    }
  }

  .is-online{
    display: none;
  }

  .burger{
    display: flex;
    padding: 0;
    width: 3.5em;
    position: relative;
    ._view2{
      opacity: 0;
    }
    img{
      @include centerAbsolute;
      width: 1.5em;
      transition: all .3s;
    }
  }

  .menu{
    position: absolute;
    top: 100%;
    margin-top: .5em;
    left: 0;
    width: 100%;
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    padding: 1.5em;
    background-color: #fff;
    box-shadow: $shadow2;
    border-radius: 1em;
    border: $border;
    flex-direction: column;
  }

  .nav{
    margin: 0;
  }

  .header-user{
    margin-top: 2em;
    .m-dropdown{
      &__show{
        text-transform: uppercase;
        font-weight: 500;
        justify-content: center;
        background-color: transparent;
        color: $onyx;
        height: unset;
        img{
          display: none;
        }
      }
      &__hidden{
        opacity: 1;
        pointer-events: unset;
        background-color: transparent;
        position: static;
        box-shadow: unset;
        border: none;
        transform: unset;
        padding: 0;
        margin-top: 1em;
      }
    }
  }
}
