.auth{
  padding: 4.5em 0 3em;
  &__wrap{
    margin: 3em auto 0;
    width: 67.25em;
    max-width: 100%;
    box-shadow: $shadow;
    background-color: #fff;
    padding: 3em;
    border-radius: 1.5em;
    &--small{
      width: 50em;
    }
  }
  &__nav{
    margin-top: 1em;
    display: flex;
    justify-content: center;
  }
  &__links{
    margin-top: 2em;
    display: flex;
    justify-content: space-between;
    &--center{
      justify-content: center;
    }
  }
  &__link{
    color: $primary;
    text-decoration: underline;
  }
  &__links2{
    margin-top: 1.5em;
  }
  &--activate{
    .m-captcha{
      margin-top: 2em;
    }
  }
}

.m-captcha{
  margin-top: 1em;
  &__img{
    margin: 0 auto;
    max-width: 19.8125em;
    display: block;
    width: 100%;
  }
}

.info-block{
  &__icon{
    width: 3.5em;
    height: 3.5em;
    @include center;
    background-color: $onyx;
    border-radius: 50%;
    margin: 0 auto 1.5em;
    img{
      width: 1.5em;
    }
  }
  &__links{
    margin-top: 1.5em;
    display: flex;
    justify-content: center;
  }
  &__desc{
    text-align: center;
    margin: 1.5em auto 0;
    width: 50em;
    max-width: 100%;
  }
  &__link{
    color: $primary;
    text-decoration: underline;
  }
}

.account{
  .auth__wrap{
    width: 100%;
  }
  &__hint{
    margin-top: 2em;
    text-align: center;
    color: $cadet-gray;
    a{
      color: $primary;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .auth{
    &__wrap{
      padding: 2.25em;
      width: 69em;
      &--small{
        width: 55em;
      }
    }
  }

  .info-block{
    &__desc{
      width: 55em;
    }
  }
}


@media screen and (max-width: 992px){
  .auth{
    &__wrap{
      padding: 1.5em;
      border-radius: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .auth{
    &__wrap{
      padding: 1.5em 1em;
      border-radius: 0;
      @include fullScreenMob;
      max-width: unset;
    }
  }
}
