.footer{
  padding: 6em 0 1.5em;
  &__inner{
    border-top: $border;
    display: flex;
    justify-content: space-between;
    padding-top: 1.5em;
  }
  &__logo{
    display: block;
    width: 10.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    margin-top: 1.5em;
  }
  &__right{
    display: flex;
  }
  &__bottom{
    margin-top: 3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .lang{
    margin-left: 10.75em;
    align-self: flex-start;
  }
}

.footer-nav{
  display: flex;
  &__group{
    @include lastM(0 3em 0 0);
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    margin-top: 1em;
    font-size: 1em;
    line-height: 150%;
  }
}

.footer-partners{
  display: flex;
  align-items: center;
  &__item{
    height: 1.5em;
    margin-right: 1em;
    &:not(:hover){
      opacity: .5;
    }
    img{
      height: 100%;
      display: block;
    }
  }
}




@media screen and (max-width: 1400px){
  .footer{
    .lang{
      margin-left: 2.25em;
    }
  }

  .footer-nav{
    &__group{
      @include lastM(0 2.25em 0 0);
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      display: block;
    }
    &__left{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .socials{
      margin: 0;
    }
    &__right{
      margin-top: 3em;
      justify-content: space-between;
    }
    &__bottom{
      margin-top: 2.25em;
      display: block;
    }
    &__c{
      margin-top: 1.5em;
      text-align: center;
    }
  }

  .footer-nav{
    &__group{
      @include lastM(0 3em 0 0);
    }
  }

  .footer-partners{
    flex-wrap: wrap;
    width: calc(100% + .75em);
    justify-content: center;
    &__item{
      margin: .75em .75em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__right{
      display: block;
      position: relative;
    }
    .lang{
      margin: 0;
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }

  .footer-nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    &__group{
      @include lastM(0);
      &--mt-48{
        margin-top: -3em !important;
      }
    }
  }
}
