.main{
  padding: .5em 1.5em 3em;
  overflow: hidden;
}

.main-slider{
  width: 1872px;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  background-color: #fff;
  padding: .5em;
  border-radius: 1.5em;
  border: $border;
  .swiper{
    &-container{
      overflow: hidden;
      border-radius: 1em;
    }
    &-pagination{
      bottom: 0;
      width: 100%;
      margin-top: .375em;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 7.25em;
      z-index: 1;
      &-bullet{
        flex-grow: 1;
        width: auto;
        border-radius: 1px;
        height: 2px;
        margin: 0 .25em !important;
        opacity: 1;
        background-color: $onyx;
        position: relative;
        span{
          background-color: $primary;
          position: absolute;
          top: 0;
          left: 0;
          transition: all .1s;
          height: 100%;
          max-width: 100%;
          border-radius: inherit;
          opacity: 0;
        }
        &-active{
          span{
            opacity: 1;
          }
        }
      }
    }
    &-button{
      padding: 0;
      width: 3.5em;
      height: 3.5em;
      svg{
        width: 1.5em;
      }
      &-prev{
        left: 1.5em;
      }
      &-next{
        right: 1.5em;
      }
    }
  }
}

.main-banner{
  padding: 5.5em 7em;
  background-size: cover;
  border-radius: 1em;
  color: #fff;
  &__inner{
    position: relative;
    z-index: 2;
    text-align: center;
    width: 50em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
}

.m-section{
  padding: 3em 0;
}

.sell{
  &__top{
    text-align: center;
  }
  &__bottom{
    margin-top: 3em;
  }
}

.exchange{
  border-radius: 1.5em;
  background-color: #fff;
  padding: 3em;
  box-shadow: $shadow;
  &__reverse{
    padding: 0;
    height: 3.5em;
  }
  &__nav{
    margin-top: 1em;
    display: flex;
    justify-content: center;
  }
  &__hint{
    margin: 2em auto 0;
    width: 33em;
    max-width: 100%;
    text-align: center;
    color: $cadet-gray;
    a{
      color: $primary;
    }
    a:not(:hover){
      text-decoration: underline;
    }
  }
}

.exchange-info{
  display: flex;
  justify-content: space-between;
  padding: 1em 1.5em;
  background: #f7f7f7;
  border: $border;
  border-radius: 1em;
  &__rate{
    font-weight: 500;
  }
  &__hint{
    margin-left: 1em;
    padding-left: 1em;
    border-left: $border;
  }
}

._flex{
  display: flex;
  align-items: center;
}

.exchange-items{
  display: grid;
  grid-template-columns: calc(50% - 2.75em) 3.5em calc(50% - 2.75em);
  gap: 1em;
  align-items: center;
  margin-top: 2em;
}

.exchange-item{
  position: relative;
  z-index: 5;
  .fg{
    position: relative;
    z-index: 2;
  }
  &._open{
    z-index: 6;
    .currency-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .currency-select{
      &__arrow{
        transform: rotate(-180deg);
      }
    }
  }
}

.currency-select{
  z-index: 2;
  width: 21.625em;
  height: calc(100% - .5em);
  top: .25em;
  right: .25em;
  background-color: $onyx;
  position: absolute;
  border-radius: .75em;
  padding: .5em 1.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  &__wrap{
    display: flex;
    align-items: center;
    color: #fff;
    margin-top: .25em;
  }
  &__icon{
    width: 1.5em;
    margin-right: .5em;
  }
  &__arrow{
    margin-left: auto;
    width: 1.5em;
    transition: all .3s;
  }
}

.currency-dropdown{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  border-radius: 1em;
  border: 2px solid $anti-flash-white;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 6em 1.5em 0;
  box-shadow: 0 .5em 1.5em rgba(49, 54, 56, 0.1);
  background-color: #fff;
  .tabs{
    max-width: 33em;
    width: 100%;
  }
  &__inner{
    margin-top: 1.5em;
    max-height: 13em;
    overflow-y: auto;
    @include scroll;
    width: calc(100% + 1em);
  }
  &__items{
    @include grid100gap(1em);
    padding-bottom: 1.5em;
  }
}

.currency-item{
  display: grid;
  align-items: center;
  grid-template-columns: 1.5em calc(50% - 1.25em) calc(50% - 1.25em);
  gap: .5em;
  color: $onyx;
  cursor: pointer;
  transition: all .3s;
  &:hover{
    color: $primary;
  }
  &--disabled{
    color: $timberwolf;
    pointer-events: none;
  }
  &__icon{
    width: 100%;
  }
  &__desc{
    color: $cadet-gray;
  }
}

.exchange-items2{
  margin-top: 1em;
}



@media screen and (max-width: 1400px){
  .main-banner{
    padding: 4em 5em;
  }

  .exchange{
    padding: 2.25em;
    border-radius: 1.125em;
  }

  .currency-select{
    width: 17.5em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 0 2em 3em;
  }

  .main-slider{
    border-radius: .75em;
    .swiper{
      &-container,
      &-slide{
        border-radius: .25em;
      }
      &-pagination{
        padding: 0 3.75em;
      }
    }
  }

  .main-banner{
    padding: 2.5em 5em;
  }

  .exchange{
    padding: 1.5em;
    border-radius: .75em;
    &__reverse{
      width: 3.5em;
      margin: 0 auto;
    }
  }

  .exchange-info{
    flex-direction: column;
    align-items: center;
    &__right{
      margin-top: 1em;
    }
  }

  .exchange-items{
    grid-template-columns: 100%;
  }

  .currency-select{
    width: 19.75em;
  }

  .exchange-items2{
    padding: 0 1.125em;
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 0 0 3em;
  }

  .main-slider{
    border-radius: 0;
    .swiper{
      &-pagination{
        padding: 0 1em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .main-banner{
    padding: 1.75em .5em;
    .container{
      padding: 0;
    }
  }

  .exchange{
    @include fullScreenMob;
    border-radius: 0;
    padding: 1.5em 1em;
  }

  .exchange-info{
    text-align: center;
    &__left{
      display: block;
    }
    &__hint{
      border-left: none;
      padding: 1em 0 0;
      margin: 1em 0 0;
      border-top: $border;
    }
  }

  .exchange-item{
    .fg {
      input{
        height: 9em;
        padding-bottom: 5.25em;
      }
      label{
        top: .75em;
      }
    }
  }

  .currency-select{
    height: 4.5em;
    top: unset;
    bottom: .25em;
    width: calc(100% - .5em);
  }

  .currency-dropdown{
    padding-top: 10em;
  }

  .exchange-items2{
    padding: 0;
  }
}
