._small-container{
  max-width: 100%;
  width: 50em;
  margin: 0 auto;
}

._medium-container{
  max-width: 100%;
  width: 67em;
  margin: 0 auto;
}


.text{
  p,li{
    font-size: 1em;
    line-height: 150%;
  }
  ul,
  p{
    @include lastM(0 0 1.5em);
  }
  ul{
    @include grid100gap(1.5em);
    padding-left: 1em;
    li{
      position: relative;
      padding-left: 1.5em;
      &:before{
        content: '';
        position: absolute;
        top: .5em;
        left: 0;
        width: .5em;
        height: .5em;
        border-radius: 50%;
        background-color: $primary;
      }
    }
  }
  h2{
    font-weight: 500;
    text-align: center;
    margin-bottom: .67em;
  }
  hr{
    display: block;
    margin: 1.375em 0;
    opacity: 0;
  }
  a{
    color: $primary;
    text-decoration: underline;
    &:hover{
      text-decoration-color: transparent;
    }
  }
}

.text._small,
.article{
  blockquote{
    @include lastM(0 0 1.25em);
    border: .5625em solid $black;
    padding: 1em 1em 1em 3.875em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 1em;
      left: 1em;
      background-image: url(../img/quote.svg);
      @include bgImgSize(100%);
      width: 2.25em;
      height: 2.25em;
    }
    p{
      font-size: 1em;
      line-height: 162%;
    }
  }
  a{
    text-decoration: underline;
    color: $primary;
  }
}

.rules,
.aml-kyc{
  &__text{
    margin-top: 3em;
  }
}

.rules{
  &__text{
    p{
      @include lastM(0);
    }
  }
}


@media screen and (max-width: 1400px){
  ._small-container{
    width: 55em;
  }

  ._medium-container{
    width: 69em;
  }
}


