@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $onyx;
    font-size: calc(1700px * (16 / 1700));
    background-color: $white-smoke;
    @media screen and (max-width: 1700px){
        font-size: calc(100vw * (16 / 1700));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    max-width: 1700px;
    width: 100%;
    margin: 0 auto;
    padding: 0 2.625em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 1400px){
    .m-page{
        font-size: calc(1392px * (16 / 1392));
        @media screen and (max-width: 1392px){
            font-size: calc(100vw * (16 / 1392));
        }
    }

    .container {
        max-width: 1392px;
        padding: 0 2em;
    }
}



@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 360));
    }

    .container {
        padding: 0 1em;
    }
}


