.how-work,
.referral-system,
.awards,
.attention{
  margin-top: 6em;
}

.how-work{
  &__steps{
    margin-top: 2em;
    padding-top: 2.25em;
  }
  &__nav{
    margin-top: 2em;
  }
}

.step-card{
  padding: 3.25em 1.5em 1.5em;
  background-color: #fff;
  border-radius: 1em;
  position: relative;
  text-align: center;
  box-shadow: $shadow;
  &__top{
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,-50%);
    width: 4.5em;
    height: 4.5em;
    background-color: $onyx;
    @include center;
    border-radius: 50%;
    color: #fff;
  }
  &__icon{
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translateX(50%);
    background-color: $primary;
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    @include center;
    img{
      width: 1.5em;
    }
  }
  &__desc{
    margin-top: 1em;
  }
}

.btn-group{
  display: flex;
  &--center{
    justify-content: center;
  }
}

.awards{
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2em;
  }
}

.award-item{
  text-align: center;
  &__desc{
    margin-top: 1em;
  }
}

.attention{
  padding-top: 1.75em;
  text-align: center;
  &__inner{
    padding: 2.75em 1.5em 1.5em;
    background-color: #fff;
    border-radius: 1em;
    position: relative;
  }
  .info-block__icon{
    margin: 0;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &__desc{
    margin-top: 1em;
  }
}


@media screen and (max-width: 1400px){

}


@media screen and (max-width: 992px){
  .how-work{
    &__steps{
      grid-template-columns: 100%;
      gap: 3.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .awards{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
