.tabs{
  display: flex;
  flex-wrap: wrap;
}

.tab{
  height: 2.5em;
  padding: 0 1em;
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 500;
  transition: all .3s;
  color: $onyx;
  margin: .5em .5em 0 0;
  background-color: $anti-flash-white;
  border-radius: .5em;
  span{
    font-size: 1em;
    line-height: 150%;
  }
  &:hover{
    cursor: pointer;
  }
  &._active,
  &:hover{
    background: $onyx;
    color: #fff;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
