.m-card{
  padding: 3em;
  background-color: #fff;
  box-shadow: $shadow;
  border-radius: 1.5em;
  &.m-card--small{
    padding: 1.5em;
    border-radius: 1em;
  }
}

.deal-info{
  margin-top: 3em;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__left{
    span{
      color: $onyx;
      font-weight: 500;
    }
  }
  &__right{
    text-align: right;
  }
  &__attention{
    margin-top: 2em;
  }
  .text{
    ul{
      gap: 0;
    }
  }
  &__bottom{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__cancel{
    font-size: 1em;
    line-height: 150%;
    color: $cadet-gray;
    text-decoration: underline;
    &:hover{
      color: $primary;
    }
  }
}

.m-num{
  padding: 1.75em 0;
  display: flex;
  justify-content: center;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    border-left: 1px dashed $onyx;
    border-right: 1px dashed $onyx;
    height: 100%;
  }
  &__inner{
    padding: 0 1em;
    @include center;
    height: 2.5em;
    background-color: $onyx;
    color: #fff;
    border-radius: .5em;
    position: relative;
    z-index: 2;
  }
  &__title{
    margin-left: .5em;
  }
  &__icon{
    margin-left: .5em;
    width: 1.5em;
  }
}

.deal-pay{
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: calc(100% - 10.5em) 9.5em;
    gap: 1em;
    align-items: flex-start;
  }
  &__code{
    padding: .5em;
    border-radius: .5em;
    background-color: $anti-flash-white;
    text-align: center;
    img{
      width: 100%;
      display: block;
    }
  }
  .fg{
    label{
      color: $cadet-gray;
    }
    .copy-btn{
      position: absolute;
      right: 1.5em;
      bottom: 1em;
    }
  }
  &__hint{
    margin-top: 2em;
    text-align: center;
  }
}

.deal-total{
  margin-top: 2em;
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    display: flex;
    justify-content: space-between;
  }
}

.m-spoiler{
  &__show{
    cursor: pointer;
  }
  &__hidden{
    display: none;
  }
}


@media screen and (max-width: 1400px){
  .m-card{
    padding: 2.25em;
    border-radius: 1.125em;
  }
}


